.card {
  width:70%;
  display: flex;
  flex-direction: row;
  margin: auto;
}
.right-card {
  display: flex;
  flex-direction: column;
  flex: 1;
}
.img {
  flex: 1;
}
img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.wrapper {
  padding-top: 100px;
  padding-bottom: 100px;
  background-color: white;
}
.img {
  margin-right: 60px;
}
.title-card h2 {
  font-size: 35px;
  padding-bottom: 10px;
}
.title-card{
  cursor: pointer;
}
.title-card span {
  margin-bottom: 15px;
  width: 50%;
  font-size: 10px;
  border-bottom: 3px solid #ff6300;
  display: block;
}
.info-card {
  padding-top: 20px;
}
.read-more button {
  transition: all 0.3s ease;
  display: flex;
  cursor: pointer;
  align-items: center;
  color: #ff6300;
  font-size: 17px;
  padding: 10px;
  border-radius: 15px;
  border: 1px solid #ff6300;
  background-color: whitesmoke;
}
.read-more {
  display: flex;
  padding-top: 20px;
}
.bottom-title h3 {
  color: #777777;
}
.read-more button:hover{
  background-color: #ff6300;
  color:white;
}
@media (min-width: 1200px) {
}

/* Landscape tablets and medium desktops */
@media (min-width: 992px) and (max-width: 1199px) {
  .text {
    width: 80%;
  }
}

/* Portrait tablets and small desktops */
@media (min-width: 768px) and (max-width: 991px) {
  .card {
    width: auto;
    flex-direction: column;
  }
  .img {
    margin: 0;
    padding-right: 30px;
    padding-left: 30px;
    margin-bottom: 30px;
  }
  .right-card {
    max-width: none;
    padding-right: 30px;
    padding-left: 30px;
    margin-bottom: 30px;
  }
}

/* Landscape phones and portrait tablets */
@media (max-width: 767px) {
  .card {
    width: auto;
    flex-direction: column;
  }
  .img {
    margin: 0;
    padding-right: 30px;
    padding-left: 30px;
    margin-bottom: 30px;
  }
  .right-card {
    max-width: none;
    padding-right: 30px;
    padding-left: 30px;
    margin-bottom: 30px;
  }
  
}

/* Portrait phones and smaller */
@media (max-width: 480px) {
  .card {
    width: auto;
    flex-direction: column;
  }
  .img {
    margin: 0;
    padding-right: 30px;
    padding-left: 30px;
    margin-bottom: 30px;
  }
  .right-card {
    max-width: none;
    padding-right: 30px;
    padding-left: 30px;
    margin-bottom: 30px;
  }
}
