.card {
  width: 70vw;
  margin: auto;
  padding-top: 100px;
  padding-bottom: 100px;
}
.wrapper {
  background-color: #f3f2ed;
}
.img {
  height: 592px;
  position: relative;
}
.title h1{
  cursor: pointer;
}
.text {
  position: absolute;
  top: 10vw;
  width: 60%;
  margin-left: 50px;
  color: white;
}
.title span {
  padding-top: 10px;
  margin-bottom: 20px;
  width: 50%;
  font-size: 10px;
  border-bottom: 3px solid #ff6300;
  display: block;
}
.title {
  font-size: 20px;
}
.desc p {
  padding-bottom: 20px;
}
.read-more {
  color: #ff6300;
}
@media (min-width: 1200px) {
}

/* Landscape tablets and medium desktops */
@media (min-width: 992px) and (max-width: 1199px) {
}

/* Portrait tablets and small desktops */
@media (min-width: 768px) and (max-width: 991px) {
  .text {
    width: 60vw;
  }
}

/* Landscape phones and portrait tablets */
@media (max-width: 767px) {
  .text {
    top: 0;
    width: 70vw;
  }
  .card {
    top: 1vh;
    width: 90vw;
  }
}

/* Portrait phones and smaller */
@media (max-width: 480px) {
  .card {
    width: 90vw;
  }
  .text {
    top: 1vh;
    margin-left: 20px;
    width: 90%;
  }
  .img{
    height:765px;
  }
}
