.main {
  display: flex;
  flex-direction: column;
  width: 70vw;
  margin: auto;
}
.wrapper {
  padding-bottom: 100px;
  padding-top: 40px;
}
.cart_container {
  background-image: url("https://www.toptal.com/designers/subtlepatterns/uploads/memphis-mini.png");
}
.icon-nation {
  display: flex;
}
.icon-nation img {
}
.nation {
  padding-top: 40px;
}
.ic {
  margin-right: 20px;
  width: 5vw;
}
.avatar {
  flex: 1;
}
.desc {
  flex: 1;
}
.container {
  padding-bottom: 50px;
  border-bottom: 1px solid #333;
  display: flex;
}
.tabs {
  padding-top: 30px;
  padding-bottom: 30px;
}
.info {
  border: 1px solid #333;
  border-radius: 8px;
  padding: 20px;
  text-align: center;
  justify-content: space-around;
  display: flex;
}
.breed {
  flex: 1;
  display: flex;
  flex-direction: column;
}
.height {
  flex: 1;
  display: flex;
  flex-direction: column;
}
.weight {
  flex: 1;
  display: flex;
  flex-direction: column;
}
.life {
  flex: 1;
  display: flex;
  flex-direction: column;
}
.img {
  margin: auto;
  width: 80px;
  height: 70px;
}
@media (min-width: 1200px) {
}

/* Landscape tablets and medium desktops */
@media (min-width: 992px) and (max-width: 1199px) {
  .main {
    width: 80vw;
  }
}

/* Portrait tablets and small desktops */
@media (min-width: 768px) and (max-width: 991px) {
  .ic {
    width: 7vw;
  }
  .main {
    width: 80vw;
  }
}

/* Landscape phones and portrait tablets */
@media (max-width: 767px) {
  .ic {
    width: 9vw;
  }
  .title {
    padding-top: 30px;
  }
  .main {
    width: 90vw;
  }
  .container {
    flex-direction: column;
  }
}

/* Portrait phones and smaller */
@media (max-width: 480px) {
  .info{
    flex-direction: column;
  }
  .breed{
    padding-bottom: 15px;
  }
  .height{
    padding-bottom: 15px;
  }
  .weight{
    padding-bottom: 15px;
  }
  .life{
    padding-bottom: 15px;
  }
  .ic {
    width: 15vw;
  }
  .title {
    padding-top: 30px;
  }
  .main {
    width: 90vw;
  }
  .container {
    flex-direction: column;
  }
}
