.wrapper {
  display: flex;
  flex-direction: column;
  background-color: #222821;
  color: white;
}
.top-foot {
  display: flex;
  list-style: none;
  justify-content: space-evenly;
  padding-bottom: 60px;
  padding-top: 60px;
}
ul {
  list-style: none;
}
.line-1 ul li:first-child {
  // margin-left:15px;
  // margin-right: 15px;
  font-size: 15px;
  font-weight: 300;
  padding-bottom: 20px;
}
.li {
  color: #a8b0a6;
  font-weight: 200;
}
.left {
  display: flex;
}
.bottom-foot {
  padding-top: 30px;
  padding-bottom: 30px;
  border-top: 2px solid #333333;
  display: flex;
  justify-content: space-around;
}
.left p:last-child {
  color: #ff6300;
}
@media (min-width: 1200px) {
}

/* Landscape tablets and medium desktops */
@media (min-width: 992px) and (max-width: 1199px) {
}

/* Portrait tablets and small desktops */
@media (min-width: 768px) and (max-width: 991px) {
}

/* Landscape phones and portrait tablets */
@media (max-width: 767px) {
  .bottom-foot {
    padding-left: 20px;
    padding-right: 20px;
  }
  .top-foot {
    padding-left: 20px;
    padding-right: 20px;
  }
}

/* Portrait phones and smaller */
@media (max-width: 480px) {
  .line-1{
    padding-bottom: 20px;
  }
  .top-foot {
    padding-left: 20px;
    padding-right: 20px;
    flex-direction: column;
  }
  .bottom-foot {
    padding-left: 20px;
    padding-right: 20px;
  }
}
