.card {
  width: 400px;
  display: grid;
  margin-right: 30px;
  margin-left: 30px;
}
.wrapper {
  background-color: white;
  padding-bottom: 100px;
}
.card-pic {
  width: 430px;
  height: 300px;
}
.img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.cards {
  display: flex;
  justify-content: center;
}
.top {
  padding-top: 100px;
  margin-left: 130px;
  padding-bottom: 70px;
}
.card-title h2 {
  font-size: 20px;
  color: #ff6300;
}
.card-title p {
  color: #d0d0d0;
}
.card-title {
//   padding-bottom: 10px;
}
.card-info {
  font-size: 15px;
  padding-bottom: 10px;
}
.title span {
  padding-top: 15px;
  margin-bottom: 10px;
  width: 10%;
  font-size: 10px;
  border-bottom: 3px solid #ff6300;
  display: block;
}
.title h1 {
  width:fit-content;
  cursor: pointer;
  font-weight: normal;
}

.bottom-title button {
  padding: 12px;
  background-color: #222222;
  color: white;
  font-size: 17px;
}
.bottom-title {
  display: flex;
  align-items: center;
}
.bottom-title p {
  padding-left: 10px;
}
.read-more{
    color:#ff6300;
}
.flex{
  display:flex;
  justify-content: center;
  flex-wrap: wrap;
}
/* Large desktops and laptops */
@media (min-width: 1200px) {
}

/* Landscape tablets and medium desktops */
@media (min-width: 992px) and (max-width: 1199px) {
  .cards{
    flex-wrap: wrap;
  }
  .card{
    padding-bottom: 20px;
  }
}

/* Portrait tablets and small desktops */
@media (min-width: 768px) and (max-width: 991px) {
  .cards{
    flex-wrap: wrap;
  }
  .top{
        margin-left: 10vw;
  }
}

/* Landscape phones and portrait tablets */
@media (max-width: 767px) {
  .cards{
    
    flex-wrap: wrap;
  }
  .card-pic{
    width:400px;
  }
  .card{
    padding-bottom: 20px;
  }
}

/* Portrait phones and smaller */
@media (max-width: 480px) {
  .cards{

    flex-wrap: wrap;
  }
  .card-pic{
    width:80vw;
  }
  .card{
    width:80vw;
    padding-bottom: 20px;
  }
  .top{
    margin-left: 20px;
  }
}


