.wrapper {
  padding-top: 60px;
  padding-bottom: 60px;
  display: flex;
  flex-direction: column;
  width: 80vw;
  margin: auto;
  background-color: white;
}
.btn button {
  margin-top: 20px;
  color: white;
  cursor: pointer;
  background-color: #ff6300;
  border-radius: 10px;
  font-size: 16px;
  border: none;
  padding: 15px;
  margin-right: 20px;
  font-weight: bold;
  transition: all 0.2s ease;
}
.title {
  font-size: 25px;
  padding-bottom: 20px;
}
.desc p {
  padding-bottom: 20px;
}
.title2 {
  font-size: 25px;
  padding-bottom: 20px;
  padding-top: 10px;
}
.desc2 {
  padding-bottom: 20px;
}
