.chart {
  display: flex;
  justify-content: center;
}
.wrapper {
  background-color: #f3f2ed;
  display: flex;
  flex-direction: column;
  padding-bottom: 100px;
}
.title h1{
  width:fit-content;
  cursor: pointer;
}
.title {
  padding-top: 100px;
  margin-left: 130px;
  padding-bottom: 70px;
}
.pie-1,
.pie-2,
.pie-3,
.pie-4 {
  margin: 20px;
  position: relative;

  padding-right: 10px;
  padding-left: 10px;
}
.background {
  border-radius: 150px;
  bottom: 32px;
  left: 21px;
  position: absolute;
  width: 135px;
  height: 135px;
  background-color: white;
  z-index: -1;
}
.title-pie {
  font-size: 20px;
  padding-top: 10px;
  padding-bottom: 10px;
  text-align: center;
}
.title span {
  margin-bottom: 15px;
  width: 10%;
  font-size: 10px;
  border-bottom: 3px solid #ff6300;
  display: block;
}
@media (min-width: 1200px) {
  .pie-1,
  .pie-2,
  .pie-3,
  .pie-4 {
    padding: 0px;
    z-index: 1;
  }
  .background {
    bottom: 14px;
    left: 7px;
    position: absolute;
    width: 153px;
    height: 152px;
  }
}

/* Landscape tablets and medium desktops */
@media (min-width: 992px) and (max-width: 1199px) {
}

/* Portrait tablets and small desktops */
@media (min-width: 768px) and (max-width: 991px) {
  .title {
    margin-left: 10vw;
  }
  .pie-1,
  .pie-2,
  .pie-3,
  .pie-4 {
    padding: 0px;
    z-index: 1;
  }
  .background {
    bottom: 14px;
    left: 7px;
    position: absolute;
    width: 153px;
    height: 152px;
  }
}

/* Landscape phones and portrait tablets */
@media (max-width: 767px) {
  .pie-1,
  .pie-2,
  .pie-3,
  .pie-4 {
    padding: 0px;
    z-index: 1;
  }
  .background {
    bottom: 28px;
    left: 7px;
    position: absolute;
    width: 22vw;
    height: 27vh;
  }
}

/* Portrait phones and smaller */
@media (max-width: 480px) {
  .pie-1,
  .pie-2,
  .pie-3,
  .pie-4 {
    z-index: 1;
  }
  .background {
    bottom: 73px;
    left: 5px;
    position: absolute;
    width: 22vw;
    height: 20vh;
  }
  .chart {
    flex-wrap: wrap;
  }
  .title{
    margin-left: 20px;
  }
}
@media (max-width: 320px) {
  .pie-1,
  .pie-2,
  .pie-3,
  .pie-4 {
    padding: 0px;
    z-index: 1;
  }
  .background {
    bottom: 114px;
    left: 13px;
    position: absolute;
    width: 62px;
    height: 64px;
  }
  .chart {
    flex-wrap: wrap;
  }
}
