.cart-container {
  display: flex;
  flex-direction: column;
}

.img {
  cursor: pointer;
  transform: scale(1);
  transition: all 0.3s ease;
}
.carts:hover {
  color: #ff6300;
}
.cart-name {
  cursor: pointer;
}
.cart-price {
  cursor: pointer;
}
.title {
  transition: all 0.3s ease;
  cursor: pointer;
  text-align: center;
}
.title:hover {
  color: #ff6300;
}
.img:hover {
  transform: scale(1.2);
}
.carts {
  transition: all 0.3s ease;
  border-bottom: 1px solid rgb(160, 160, 160);
  width: 700px;
  margin: auto;
  // background-color: rgb(163, 163, 163);
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding-top: 50px;
  padding-bottom: 50px;
  // border-radius: 20px;
}
.all-cart {
  // border-radius: 20px;
  border-left: 1px solid #333;
  border-right: 1px solid #333;

  max-height: 100%;
  flex-direction: column;
  width: 800px;
  margin: auto;
  // background-color: rgb(163, 163, 163);
  display: flex;
}
.cart-header {
  padding-bottom: 50px;
}
.cart_container {
  padding-bottom: 100px;
}
.cart-img {
  width: 100px;
  height: 100px;
}
.img {
  border-radius: 50%;
  height: 100%;
  width: 100%;
  object-fit: cover;
}
@media (min-width: 1200px) {
}

/* Landscape tablets and medium desktops */
@media (min-width: 992px) and (max-width: 1199px) {
}

/* Portrait tablets and small desktops */
@media (min-width: 768px) and (max-width: 991px) {
  .carts {
    width: auto;
    flex-direction: column;
  }
  .all-cart {
    width: auto;
  }
}

/* Landscape phones and portrait tablets */
@media (max-width: 767px) {
  .carts {
    width: auto;
    flex-direction: column;
  }
  .all-cart {
    width: auto;
  }
}

/* Portrait phones and smaller */
@media (max-width: 480px) {
  .carts {
    width: auto;
    flex-direction: column;
  }
  .all-cart {
    width: auto;
  }
}
