.img {
  width: 100%;
  height: calc(100vh - 0px);
  object-fit: cover;
  position: relative;
}
.info {
  position: absolute;
  bottom: 30%;
  left: 20vw;
  color: white;
}
.donate {
  color: black;
}
// .flex{
//     display:flex;
//     align-items: center;
// }
.button {
  color: white;
  cursor: pointer;
  background-color: #ff6300;
  border-radius: 10px;
  font-size: 16px;
  border: none;
  padding: 15px;
  margin-right: 20px;
  font-weight: bold;
  transition: all 0.2s ease;
}
.info-text {
  font-size: 18px;
  padding-bottom: 20px;
}
.info-title {
  font-size: 50px;
  width: 550px;
}
.button:hover {
  background-color: #50cc58;
}
@media (min-width: 1200px) {
  .info {
    bottom: 30%;
  }
  .info-text {
    font-size: 25px;
  }
  .info-title {
    width: 877px;
    font-size: 70px;
  }
}

