/* Landscape phones and portrait tablets */
@media (max-width: 767px) {

  .info {
        width: 80vw;
    height: 59vh;
    // bottom: -10%;
    left: 0vw;
  }
  .info-title {
    width: 100vw;
  }
  .img {
    padding-top: 10vh;
    height: auto;
  }
}

/* Portrait phones and smaller */
@media (max-width: 480px) {
  .info-title {
    width: 100vw;
  }
  .info {
    left: 0px;
    bottom: 10%;
  }
  .img {
    // padding-top: 24vh;
    height: 100vh;
  }
}
@media (max-width: 320px) {
  .img {
    // padding-top: 44vh;
