.navbar {
  //stickybar
  position: fixed;
  width: 100vw;
  //------------
  z-index: 2;
  border-bottom: 3px solid brown;
  padding-top: 10px;
  padding-bottom: 10px;
  background-color: rgba(245, 245, 245, 0.411);
  // display: flex;
  // // flex-direction: row;
  // // overflow: hidden;
  // justify-content: space-around;
}

.navbar > * {
  text-decoration: none;
  color: black;
}
.dropdown {
  margin-right: 30px;
  float: left;
  overflow: hidden;
  border-bottom: 3px solid transparent;
  transition: all 0.3s ease;
}
.dropdown:hover {
  color: #ff6300;
  border-bottom: 3px solid #ff6300;
}
.animal-project:hover {
  color: #ff6300;
  border-bottom: 3px solid #ff6300;
}

.news:hover {
  color: #ff6300;
  border-bottom: 3px solid #ff6300;
}
.buy-dog:hover {
  color: #ff6300;
  border-bottom: 3px solid #ff6300;
}

.dropdown .dropbtn {
  font-size: 17px;
  display: flex;
  align-items: center;
  border: none;
  outline: none;
  background-color: inherit;
  font-family: inherit; /* Important for vertical align on mobile phones */
  margin: 0; /* Important for vertical align on mobile phones */
}
.bars {
  display: none;
}
.dropdown-content .link:hover {
  color: #ff6300;
}
.dropdown-content {
  margin-top: 3px;
  transition: all 0.2s ease;
  // display: flex;
  // flex-direction: column;
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}
.dropdown-content .link {
  transition: all 0.3s ease;
  float: none;
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  text-align: left;
}
.dropdown:hover .dropdown-content {
  display: block;
}
.logo {
  width: 50px;
  height: 50px;
  margin-right: 10px;
}
.left-nav {
  justify-content: space-around;
  // padding-right: 50px;
  display: flex;
  align-items: center;
}
.logo-name p:first-child {
  font-weight: 500;
  font-size: 20px;
}
.logo-name p:last-child {
  letter-spacing: 0px;
  font-size: 13px;

  font-weight: 100;
}
.mid-nav {
  font-size: 17px;
  align-items: center;
  display: flex;
}
.animal-project {
  border-bottom: 3px solid transparent;
  transition: all 0.3s ease;

  margin-right: 30px;
}
.news {
      margin-right: 30px;
  transition: all 0.3s ease;

  border-bottom: 3px solid transparent;

}
.cart {
  transition: all 0.3s ease;

  border-bottom: 3px solid transparent;

  display: flex;
  align-items: center;
}
.buy-dog {
      margin-right: 30px;
  transition: all 0.3s ease;

  border-bottom: 3px solid transparent;
}
