@media (min-width: 1200px) {
}

/* Landscape tablets and medium desktops */
@media (min-width: 992px) and (max-width: 1199px) {
}

/* Portrait tablets and small desktops */
@media (min-width: 768px) and (max-width: 991px) {
  .dropdown {
    width: fit-content;
  }
  .wrapper {
    position: fixed;
    width: 100%;
    z-index: 10;
  }
  .bars {
    align-items: center;
    display: block;
  }
  .cart {
    display: none;
  }
  .navbar {
    justify-content: end;
  }
  .left-nav {
    width: 100%;
    display: flex;
    flex-direction: column;
  }
  .dropbtn {
    border-bottom: 3px solid black;
    padding-top: 10px;
    padding-bottom: 10px;
  }
  .animal-project {
    width: fit-content;
    padding-top: 10px;
    padding-bottom: 10px;
  }
  .news {
    width: fit-content;
    padding-top: 10px;
    padding-bottom: 10px;
  }
  .buy-dog {
    width: fit-content;
    padding-top: 10px;
    padding-bottom: 10px;
  }

  .logo-name p:last-child{
    display:none;
  }
  .flex2 {
    transition: all 1s ease;

    align-items: center;
    padding-left: 20px;
    padding-right: 20px;
    width: 100%;
    display: flex;
    justify-content: space-between;
  }
  .info {
    bottom: 20%;
  }
}

