.card {
  display: flex;
  flex-direction: column;
}
.top_card {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  padding-bottom: 20px;
}
.right_top_card {
   height: 122px;
  text-align: center;
  justify-content: center;
  display: flex;
  flex-direction: column;
}
.mid_card {
  overflow: hidden;
  height: 120px;
  text-overflow: ellipsis;
  padding-bottom: 30px;
  text-align: center;
}
.bottom_card {
  padding-top: 30px;
  padding-bottom: 30px;
  text-align: center;
}
.img {
  border-radius: 50%;
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.image_card {
  width: 140px;
  height: 100px;
}
.add-cart {
  cursor: pointer;
  padding: 10px;
  font-size: 20px;
  margin: auto;
  border: none;
  border-radius: 20px;
  background-color: rgb(212, 212, 212);
  width: 50%;
  transition: all 0.3s ease;
}
.add-cart:hover {
  color: aliceblue;
  background-color: rgb(46, 46, 46);
}
.add-cart:focus {
  background-color: #ff6300;
}
.right-top-card h2 {
  justify-content: flex-start;
}
@media (min-width: 1200px) {
}

/* Landscape tablets and medium desktops */
@media (min-width: 992px) and (max-width: 1199px) {
  .img {
    border: 1px solid #333;
    border-radius: 5px;
  }

  .add-cart:focus {
    background-color: #ff6300;
  }
  .image_card {
    width: auto;
    height: 115px;
  }
  .top_card {
    flex-direction: column;
  }
  .right_top_card {
    height: 122px;
    width: auto !important;
  }
}

/* Portrait tablets and small desktops */
@media (min-width: 768px) and (max-width: 991px) {
  .img {
    border: 1px solid #333;
    border-radius: 5px;
  }

  .add-cart:focus {
    background-color: #ff6300;
  }
  .image_card {
    width: auto;
    height: 115px;
  }
  .top_card {
    flex-direction: column;
  }
  .right_top_card {
    height: 122px;
    width: auto !important;
  }
}

/* Landscape phones and portrait tablets */
@media (max-width: 767px) {

  .img {
    border: 1px solid #333;
