.img {
  width: 40vw;
  height: 60vh;
  padding-bottom: 20px;
}
.card {
  // border-right: 1px solid rgb(118 118 118);;
  padding-right: 3vw;
  margin: auto;
  // width:40vw;
}
.desc {
  // width: 40vw;
}
.input {
  display: flex;
}
.wrapper {
  padding-top: 100px;
  width: 60vw;
  margin: auto;
  display: flex;
}
.right-side {
  display: flex;
  flex-direction: column;
}
.all {
  margin: auto;
  display: flex;
}
.title {
  padding-bottom: 20px;
}
.title-card {
  padding-top: 10px;
  padding-bottom: 10px;
}
.desc-card {
  padding-top: 10px;
  padding-bottom: 10px;
}
.read-more {
  padding-top: 10px;
  padding-bottom: 10px;
}
.wrap {
  display: flex;
  flex-wrap: wrap;
}
.left-side {
  border-bottom: 1px solid rgb(118 118 118);
  padding-bottom: 20px;
}
.title-card p {
  color: #ff6300;
}
.read-more p {
  color: #ff6300;
}
.recent {
  padding-top: 50px;
  display: flex;
  flex-direction: column;
}
.recent p {
  cursor: pointer;
  padding-top: 10px;
  color: #ff6300;
}
.input input {
  margin-right: 10px;
  border-radius: 10px;
  padding: 10px;
  border: 1px solid rgb(118 118 118);
}
.input button {
  padding: 10px;
  border-radius: 10px;
  border: 1px solid rgb(118 118 118);
}
.button {
  color: white;
  cursor: pointer;
  background-color: #ff6300;
  border-radius: 10px;
  font-size: 16px;
  border: none;
  padding: 15px;
  margin-right: 20px;
  font-weight: bold;
  transition: all 0.2s ease;
  margin-bottom: 24px;
}
.valid-form {
  display: flex;
  flex-direction: column;
}
.bottom-form {
  margin-bottom: 20px;
  display: flex;
}

.top-form {
  margin-bottom: 20px;
}

textarea {
  padding: 10px;
}
.title-form {
  margin-bottom: 15px;
}

@media (min-width: 1200px) {
}

/* Landscape tablets and medium desktops */
@media (min-width: 992px) and (max-width: 1199px) {
  .wrapper {
    width: 100vw;
    padding-left:5vw;
    padding-right: 5vw;
  }
}

/* Portrait tablets and small desktops */
@media (min-width: 768px) and (max-width: 991px) {
  .wrapper {
    flex-direction: column;
    width: 85vw;
  }
  .desc {
    width: none;
  }
  .img {
    width: 80vw;
  }
  .right-side {
    padding-top: 20px;
    padding-bottom: 40px;
  }
  .title-card {
    padding-top: 60px;
  }
}

/* Landscape phones and portrait tablets */
@media (max-width: 767px) {
  .bottom-form {
    flex-direction: column;
  }
  .wrapper {
    flex-direction: column;
    width: 90vw;
  }
  .title-card {
    padding-top: 60px;
  }
  .desc {
    width: 90vw;
  }
  .img {
    width: 80vw;
  }
  .right-side {
    padding-top: 20px;
    padding-bottom: 40px;
  }
  .card {
    padding: 0px;
  }
  .search-input {
    padding-bottom: 10px;
  }
}

/* Portrait phones and smaller */
@media (max-width: 480px) {
  .search-input {
    padding-bottom: 10px;
  }
  .wrapper {
    flex-direction: column;
    width: 90vw;
  }
  .title-card {
    padding-top: 60px;
  }
  .desc {
    width: 90vw;
  }
  .img {
    width: 90vw;
  }
  .right-side {
    padding-top: 20px;
    padding-bottom: 40px;
  }
  .card {
    padding: 0px;
  }
  .bottom-form {
    flex-direction: column;
  }
}
