.wrapper {
  display: flex;
  flex-direction: column;
}
.valid-form {
  padding-left: 20px;
  padding-right: 20px;
  justify-content: center;
  display: flex;
}
.content h2 {
  text-align: center;
}
.content {
  padding-top: 20px;
  padding-bottom: 40px;
}
.left-form {
  padding-right: 60px;
}
.valid-form h3 {
  padding-bottom: 10px;
  padding-top: 10px;
  color: #ff6300;
}
.email {
  padding-bottom: 10px;
  padding-top: 10px;
  display: flex;
}
.email p {
  color: #757575;

  cursor: pointer;
  color: #ff6300;
}
.address {
  padding-bottom: 10px;
  padding-top: 10px;
  display: flex;
}
.address p {
  color: #757575;
}
.hours {
  padding-bottom: 10px;
  padding-top: 10px;
  display: flex;
}
.hours p {
  color: #757575;
}
.phone {
  padding-bottom: 10px;
  padding-top: 10px;
  display: flex;
}
.phone p {
  color: #757575;

  cursor: pointer;
}
.nameInput {
  padding: 14px;
  font-size: 17px;
  width: 100%;
}
.emailInput {
  padding: 14px;
  font-size: 17px;
  width: 100%;
}
.messageInput {
  padding: 14px;
  font-size: 17px;
  width: 100%;
}
.right-form {
  color: #757575;
}
.right-form button {
  width: 100%;
  border: none;
  background-color: #ff6300;
  color: white;
  font-size: 15px;
  border-radius: 5px;
  height: 40px;
  margin-top: 10px;
}
@media (min-width: 1200px) {
