.background {
  height: 100vh;
  z-index: 0;
  position: relative;
}
.title-donate p {
  padding-top: 10px;
  color: #6f6f6f;
}
.title-donate {
  padding-bottom: 20px;
}
.input-donate {
  padding-bottom: 20px;
}
.input-donate input {
  padding: 20px;
  border-radius: 5px;
  border: 2px solid #6f6f6f;
  font-size: 20px;
}
.button {
  margin-top: 20px;
  color: white;
  cursor: pointer;
  background-color: #ff6300;
  border-radius: 10px;
  font-size: 16px;
  border: none;
  padding: 15px;
  margin-right: 20px;
  font-weight: bold;
  transition: all 0.2s ease;
}
.button:focus {
  background-color: #28c77b;
}
.input-donate input:focus {
  border-radius: 5px;
  border: 2px solid #28c77b;
}
.card-donate {
  border-radius: 10px;
  position: absolute;
  top: 15vh;
  left: 20vh;
  width: 35vw;
  height: 65vh;
  background-color: white;
  padding: 20px;
  text-align: center;
}
.money-donate {
  display: grid;
  grid-template-columns: auto auto auto;
  // display: flex;
  // flex-wrap: wrap;
}
