.line {
  padding-top: 60px;
  display: flex;
  margin: auto;
  justify-content: center;
  padding-bottom: 100px;
}

.logo {
  position: relative;
}
.wrapper {
  background-color: white;
}
.card-1 {
  width: 250px;
  text-align: center;
}
.title span {
  padding-top: 10px;
  margin-bottom: 20px;
  margin: auto;
  width: 10%;
  font-size: 10px;
  border-bottom: 3px solid #ff6300;
  display: block;
}
.title h1{
  width:fit-content;
  margin: auto;
    cursor: pointer;

}

.title {

  padding-top: 100px;
  text-align: center;
  margin: auto;
}
.shadow-logo {
  position: absolute;
  width: 40px;
  height: 40px;
  border-radius: 50px;
  background-color: #f3f2ed;
  z-index: 0;
  left: 42%;
  bottom: -2px;
}
// .icon{
//     z-index: 5;
//     position: relative;
// }
.amount {
  padding-top: 20px;
  font-size: 20px;
  padding-bottom: 10px;
}
.name {
  color: rgb(97, 97, 97);
}
@media (min-width: 1200px) {
}

/* Landscape tablets and medium desktops */
@media (min-width: 992px) and (max-width: 1199px) {
}

/* Portrait tablets and small desktops */
@media (min-width: 768px) and (max-width: 991px) {
}

/* Landscape phones and portrait tablets */
@media (max-width: 767px) {
  .line {
    flex-direction: column;
  }
  .card-1 {
    margin: auto;
    padding-top: 20px;
    padding-bottom: 20px;
  }
}

/* Portrait phones and smaller */
@media (max-width: 480px) {
  .line {
    flex-direction: column;
  }
  .card-1 {
    padding-top: 20px;
    padding-bottom: 20px;
    margin: auto;
  }
}
