*,
*::before,
*::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
*{
    text-decoration: none;
    color:inherit;
}
textarea{
  border-radius: 10px;

}

button{
  cursor: pointer;
}
input{
  border:1px solid #333;
  border-radius: 10px;
}
body {
  text-decoration: none;
  background-color: #fae1dd;
  scroll-behavior: smooth;
  line-height: 1.5;
  font-family: "Inter", sans-serif;
}
img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}
input,
button,
textarea,
select {
  outline: none;
}
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}
