.img {
  width: 40vw;
  height: 60vh;
  padding-bottom: 20px;
}
.card {
    // border-right: 1px solid rgb(118 118 118);;

  padding-right: 3vw;
  margin: auto;
  // width:40vw;
}
.input {
  display: flex;
}
.wrapper {
  // width:60vw;
  // margin:auto;
  display: flex;
}
.right-side {
  padding-left: 3vw;
  display: flex;
  flex-direction: column;
}
.all {
  margin: auto;
  display: flex;
}
.title {
  padding-bottom: 20px;
}
.title-card {
  padding-top: 10px;
  padding-bottom: 10px;
}
.desc-card {
  padding-top: 10px;
  padding-bottom: 10px;
}
.read-more {
  padding-top: 10px;
  padding-bottom: 10px;
}
.wrap {
  display: flex;
  flex-wrap: wrap;
}
.left-side {
  padding-top: 40px;
  border-bottom: 1px solid rgb(118 118 118);;
  padding-bottom: 20px;
}
.title-card p {
  color: #ff6300;
}
.read-more p {
  color: #ff6300;
}
.recent {
  display: flex;
  flex-direction: column;
}
.recent p {
  cursor: pointer;
  padding-top: 10px;
  color: #ff6300;
}
@media (min-width: 1200px) {


}

/* Landscape tablets and medium desktops */
@media (min-width: 992px) and (max-width: 1199px) {
  .img{
  height:80vh;
}
.img{
  width:60vw;
}
}

/* Portrait tablets and small desktops */
@media (min-width: 768px) and (max-width: 991px) {

.img{
  height:50vh;
  width:60vw;
}
}

/* Landscape phones and portrait tablets */
@media (max-width: 767px) {
.img{
  height:50vh;
  width:80vw;
}
}

/* Portrait phones and smaller */
@media (max-width: 480px) {
  .img{
  height:30vh;
  width:75vw;
}
}
@media (max-width: 320px) {
  .img{
  height:30vh;
  width:80vw;
}
}
