.logo {
  width: 170px;
  height: 157px;
  margin: auto;
  text-align: center;
}
.below-logo {
  padding-top: 15px;
}
.wrapper {
  background-color: #f3f2ed;
  padding-top: 60px;
  padding-bottom: 60px;
  display: flex;
  flex-direction: column;
  text-align: center;
  justify-content: center;
}
.bottom {
  padding-top: 30px;
}
.bottom h1 {
  width:fit-content;
  margin:auto;
  cursor: pointer;
  padding-bottom: 20px;
}
.bottom button {
    margin:auto;
    display:flex;
    color:white;
  background-color: #ff6300;
  border: 1px solid #ff6300;
  border-radius: 10px;
  padding: 12px;
  font-size: 20px;
  transform: scale(1);
  cursor: pointer;
  transition: all 0.3s ease;
}
.bottom button:hover {
  transform: scale(1.2);
}
