.title-card p {
  color: #ff6300;
}
.read-more p {
  color: #ff6300;
}
.right-side h2 {
  padding-top: 10px;
}
.recent {
  padding-top: 10px;
  display: flex;
  flex-direction: column;
}
.recent p {
  cursor: pointer;
  // padding-top: 10px;
  color: #ff6300;
}
.input {
  display: flex;
}
.input input {
  margin-right: 10px;
  border-radius: 10px;
  padding: 10px;
  border: 1px solid rgb(118 118 118);
}
.input button {
  padding: 10px;
  border-radius: 10px;
  border: 1px solid rgb(118 118 118);
}
@media (min-width: 1200px) {

}

/* Landscape tablets and medium desktops */
@media (min-width: 992px) and (max-width: 1199px) {
  .all{
    padding-left: 5vw;
    padding-right: 5vw;
    width:100vw;
  }
}

/* Portrait tablets and small desktops */
@media (min-width: 768px) and (max-width: 991px) {
  .all {
    flex-direction: column;
  }
  .right-side {
    padding-top: 20px;
    padding-bottom: 20px;
  }
  .card{
    padding-top: 10vh;
  }
}

/* Landscape phones and portrait tablets */
@media (max-width: 767px) {

  .all {
    width:80vw;
    flex-direction: column;
  }
  .right-side {
    padding-top: 20px;
    padding-bottom: 20px;
  }
  .card{
    padding-top: 10vh;
  }
}

/* Portrait phones and smaller */
@media (max-width: 480px) {
  
    .all {
      width:80vw;
    flex-direction: column;
  }
  .right-side {
    padding-top: 20px;
    padding-bottom: 20px;
  }
  .card{
    padding-top: 10vh;
  }
}
@media (max-width: 320px) {
  .input{
    flex-wrap: wrap;
  }
  .input button{
    margin-top: 5px;
  }
}
